import React from 'react';
import styles from './ObraCard.module.css'; // Asegrate de tener un archivo CSS para los estilos
import { Link } from 'react-router-dom';

const ObraCard = ({ obra }) => {
  const { id, nombre, ao, imagenes, lugar } = obra;

  return (
    <div className={styles.obraCard}>
      <img src={imagenes[0]} alt={`Imagen de ${nombre}`} className={styles.image} />
      <div className={styles.details}>
        <h2>{nombre}</h2>
        <p>{ao}</p>
        <p>{lugar}</p>
        <Link to={`/arquitectura/obras-terminadas/${id}`} className={styles.link}>
          <button>Ms Detalle</button>
        </Link>
      </div>

    </div>
  );
};

export default ObraCard;

